<template>
  <v-container fluid grid-list-md>
    <v-layout row>
      <v-flex xs12>
      </v-flex>
       <v-flex xs12 sm8>
         <v-card>
          <v-card-title>Информация</v-card-title>
          <v-card-text>
            <canvas id="myChart"></canvas>
          </v-card-text>
        </v-card>
       </v-flex>

      <v-flex xs12 sm4>
        <v-card>
          <v-card-title>Прибыль составила: <span class="green--text">{{info.profit}}</span> <v-icon color="success">mdi-currency-rub</v-icon></v-card-title>
        </v-card>

        <v-card class="my-2">
          <v-card-title>Клиентов всего: {{info.clients}}</v-card-title>
        </v-card>

        <v-card class="my-2">
          <v-card-title>Заказов всего: {{info.orders}}</v-card-title>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import Chart from 'chart.js'
import { setTimeout } from 'timers';
export default {
  name: 'Home',
  mounted() {
    var ctx = document.getElementById('myChart')
    ctx.getContext('2d')
    var myChart = new Chart(ctx, {
        type: 'bar',
        data: {
            labels: ['Red', 'Blue', 'Yellow', 'Green', 'Purple', 'Orange'],
            datasets: [{
                label: '# of Votes',
                data: [12, 19, 3, 5, 2, 3],
                backgroundColor: [
                    'rgba(255, 99, 132, 0.2)',
                    'rgba(54, 162, 235, 0.2)',
                    'rgba(255, 206, 86, 0.2)',
                    'rgba(75, 192, 192, 0.2)',
                    'rgba(153, 102, 255, 0.2)',
                    'rgba(255, 159, 64, 0.2)'
                ],
                borderColor: [
                    'rgba(255, 99, 132, 1)',
                    'rgba(54, 162, 235, 1)',
                    'rgba(255, 206, 86, 1)',
                    'rgba(75, 192, 192, 1)',
                    'rgba(153, 102, 255, 1)',
                    'rgba(255, 159, 64, 1)'
                ],
                borderWidth: 1
            }]
        },
        options: {
          responsive: true,
          maintainAspectRatio: true,
            scales: {
                yAxes: [{
                    ticks: {
                        beginAtZero: true
                    }
                }]
            }
        }
    });
  },
  computed: {
    info() {
      return this.$store.getters.getAllInfo
    }
  }
}
</script>
